<template>
  <div :style="{width: width}" class="tooltipselector">
    <img
      :id="'_' + r"
      class="champ-icon lozad"
      :style="{width: width}"
      :class="'tier' + champObject[champ.name].cost"
      :src="getAvatarURL(champ.name, 2)"
      :alt="champ.name"
      :title="champ.name"
      :data-name="champ.name"
      :data-set="2"
      :draggable="draggable"
      data-toggle="tooltip"
      data-html="true"
      :data-placement="index > 80 ? 'auto' : 'top'"
      :data-original-title="info"
      @dragstart="drag($event)"
    />
    <!-- <div
          class="demo m-1"
          v-for="c in listChamp.filter(c => c.set[0] == 2)"
          :key="c.id"
          :data-name="c.name"
          :data-set="c.set[0]"
          v-on:dragstart="drag($event)"
          :draggable="true"
    ></div>-->
    <div v-if="showItems" class="items d-flex justify-content-center my-1">
      <div v-for="item in champ.items" :key="uuid(item)">
        <img
          :id="'item-' + r + getName(item)"
          class="item lozad"
          :src="getItemURL(item)"
          :alt="item"
          data-toggle="tooltip"
          data-html="true"
          data-placement="right"
          :data-original-title="infoItem[r+getName(item)] || ''"
        />
        <div class="d-none">
          <ItemInfo
            v-once
            :id="'itemInfo-' + r + getName(item)"
            :name="item"
            :type="isCombineOrInfo(item)"
          />
        </div>
      </div>
    </div>
    <div class="d-none">
      <ChampInfo :id="'info-' + getName(champ.name)" :champ="champObject[champ.name]" />
    </div>
  </div>
</template>

<script>
import ChampInfo from "./ChampInfo";
import ItemInfo from "./ItemInfo";

export default {
  name: "ChampIcon",
  components: { ChampInfo, ItemInfo },
  props: {
    champ: Object,
    draggable: {
      type: Boolean,
      default: false
    },
    showItems: {
      type: Boolean,
      default: false
    },
    showInfo: {
      type: Boolean,
      default: false
    },
    index: Number,
    width: {
      type: String,
      default: "50px"
    }
  },
  data() {
    return {
      r: this.uuid(1),
      info: "",
      infoItem: {},
      imageSlice: "1 !important"
    };
  },
  created() {
    // const observer = lozad(); // lazy loads elements with default selector as '.lozad'
    // observer.observe();
  },
  mounted() {
    if (document.querySelector("#info-" + this.getName(this.champ.name))) {
      this.info = document.querySelector(
        "#info-" + this.getName(this.champ.name)
      ).outerHTML;
      document.querySelector("#info-" + this.getName(this.champ.name)).parentElement.remove();
    }

    if (document.querySelectorAll(`[id*=itemInfo-${this.r}]`).length) {
      document.querySelectorAll(`[id*=itemInfo-${this.r}]`).forEach(e => {
        this.infoItem[e.id.substr(9)] = e.innerHTML;
        e.remove();
        // console.log(e)
      });
    }
    let buttonsWithOpenTooltips = [],
      mousedOverTooltip,
      isTooltipClickablePreference = true;

    function hideOpenTooltips() {
      for (let button of buttonsWithOpenTooltips) {
        $(button).tooltip("hide");
      }
      buttonsWithOpenTooltips = [];
    }
    $(
      "#icon-" +
        this.getName(this.champ.name) +
        ` #_${this.r}[data-toggle="tooltip"]`
    )
      .tooltip({ trigger: "manual", boundary: "window" })
      .on("mouseenter touchend focus", function() {
        // Hide all other open tooltips
        buttonsWithOpenTooltips.push(this);

        $(this).tooltip("show");

        $(".tooltip")
          .on("mouseenter focus", function() {
            // Track last button moused over
            mousedOverTooltip = this;
          })
          .on("mouseleave", function() {
            // Hide this tooltip when user mouses out of it
            mousedOverTooltip = null;
            hideOpenTooltips();
          })
          .on("click", function() {
            // Hide this tooltip when user clicks it
            hideOpenTooltips();
          });
      })
      .on("mouseleave", function() {
        // .setTimeout needed because tooltip mouseenter fires after button mouseleave
        let _this = this;
        // isTooltipClickablePreference = uiUtils.getPreference('tooltip-clickable', false);
        if (isTooltipClickablePreference) {
          window.setTimeout(function() {
            if (
              $(".tooltip").length > 0 &&
              !$(".tooltip").is($(mousedOverTooltip))
            ) {
              $(_this).tooltip("hide");
            }
          }, 100);
        } else {
          $(_this).tooltip("hide");
        }
      });

    let that = this;
    // if (this.showItems && this.champ.items) {

    // this.champ.items.forEach(item => {
    // $("#item-" + that.getName(item) + that.r)
    $("[id*=item-]").tooltip();
    // $(document.querySelectorAll("[id*=item-" + this.r + "]"))
    //   .tooltip({ trigger: "manual", boundary: "window" })
    //   .on("mouseenter touchend focus", function() {
    //     // Hide all other open tooltips
    //     $(this).tooltip("show");
    //     buttonsWithOpenTooltips.push(this);

    //     $(".tooltip")
    //       .on("mouseenter focus", function() {
    //         // Track last button moused over
    //         mousedOverTooltip = this;
    //       })
    //       .on("mouseleave", function() {
    //         // Hide this tooltip when user mouses out of it
    //         mousedOverTooltip = null;
    //         hideOpenTooltips();
    //       })
    //       .on("click", function() {
    //         // Hide this tooltip when user clicks it
    //         hideOpenTooltips();
    //       });
    //   })
    //   .on("mouseleave", function() {
    //     // .setTimeout needed because tooltip mouseenter fires after button mouseleave
    //     let _this = this;
    //     // isTooltipClickablePreference = uiUtils.getPreference('tooltip-clickable', false);
    //     if (isTooltipClickablePreference) {
    //       window.setTimeout(function() {
    //         if (
    //           $(".tooltip").length > 0 &&
    //           !$(".tooltip").is($(mousedOverTooltip))
    //         ) {
    //           $(_this).tooltip("hide");
    //         }
    //       }, 100);
    //     } else {
    //       $(_this).tooltip("hide");
    //     }
    //   });
    // });
    // }

    this.iconCountF();
  },
  methods: {
    drag: function(ev) {
      ev.dataTransfer.setData("name", ev.target.dataset.name);
      ev.dataTransfer.setData("set", ev.target.dataset.set);
    },
    isCombineOrInfo: function(item) {
      return item.into ? "into" : "combine";
    }
  }
};
</script>

<style>
.demo {
  height: 50px;
  width: 50px;
  display: inline-block;
  background: black;
}
.icon {
  width: 50px;
  height: auto;
}
.item {
  width: 20px;
  height: auto;
}

.tooltipselector:hover {
  border-color: orangered;
}
</style>