<template>
  <div v-if="type == 'origin'" class="text-white text-left p-1" style="max-width: 300px">
    <div class="p-1">
      <img :src="getIconURL(name)" :alt="name" width="30px" />
      <h6 v-translate="name" class="mx-2 d-inline">
        {{ name }}
      </h6>
    </div>
    <div v-translate="originObject[name].effect" class>
      {{ originObject[name].effect }}
    </div>
    <div
      v-for="bonus in originObject[name].bonus"
      :key="uuid(bonus)"
      class="border-dark border-top border-bottom px-2"
    >
      <span class="badge" :class="[sy.count >= bonus.count ? 'badge-danger' : '']">{{ bonus.count }}</span>
      <!-- {{ bonus.value }} -->
      <div v-translate="bonus.value" class="p-1 d-inline"></div>
    </div>
    <div class>
      <!-- <div class="btn px-0 text-white">Champions:</div> -->
      <div class="btn px-0 text-white">
        Tướng:
      </div>

      <img
        v-for="c in origins[name]"
        :key="uuid(c)"
        :src="getAvatarURL(c.name, 2)"
        :alt="c.name"
        class="ml-1 champ-icon lozad"
        width="30px"
        height="30px"
        :class="checkTeam(c.name) ? 'in-team' : 'not-in-team'"
      />
      <!-- <ChampIcon v-for="c in origins[name]" :key="uuid(c)" :champ="c" /> -->
    </div>
  </div>
  <div v-else-if="type == 'class'" class="text-white text-left p-1" style="max-width: 300px">
    <div class>
      <img class="lozad" :src="getIconURL(name)" :alt="name" width="30px" />
      <h6 v-translate="name" class="mx-2 d-inline">
        {{ name }}
      </h6>
    </div>
    <div v-translate="classObject[name].effect" class>
      {{ classObject[name].effect }}
    </div>
    <div
      v-for="bonus in classObject[name].bonus"
      :key="uuid(bonus)"
      class="border-dark border-top border-bottom px-2"
    >
      <span class="badge" :class="[sy.count >= bonus.count ? 'badge-danger' : '']">{{ bonus.count }}</span>
      <!-- {{ bonus.value }} -->
      <div v-translate="bonus.value" class="p-1 d-inline"></div>
    </div>
    <div class>
      <!-- <div class="btn px-0 text-white">Champions:</div> -->
      <div class="text-center text-white d-inline">
        Tướng:
      </div>

      <img
        v-for="c in classes[name]"
        :id="'not-' + c.name.split(' ')[0]"
        :key="uuid(c)"
        :src="getAvatarURL(c.name, 2)"
        :alt="c.name"
        class="ml-1 lozad"
        width="30px"
        height="30px"
        :class="checkTeam(c.name) ? 'in-team' : 'not-in-team'"
      />
      <!-- <ChampIcon v-for="c in classes[name]" :key="uuid(c)" :champ="c" /> -->
    </div>
  </div>
</template>

<script>
import ChampIcon from "./ChampIcon";

export default {
  name: "TraitInfo",
  components: { ChampIcon },
  props: {
    name: String,
    type: String,
    set: {
      type: Number,
      default: 2
    },
    sy: Object,
    team: Array
  },
  created() {
    // const observer = lozad(); // lazy loads elements with default selector as '.lozad'
    // observer.observe();
  },
  methods: {
    checkTeam: function(name) {
      let a = false;
      this.team.forEach(t => {
        if (t.name == name) {
          a = true;
        }
      });
      return a;
    }
  }
};
</script>

<style>
.not-in-team {
  filter: grayscale(1);
}
.in-team {
  border: 1px solid orange;
}
</style>