<template>
  <div
    v-if="set == 2"
    id="temp-comp"
    class="bg-dark border"
    :class="[team.tier == 1 ? 'border-danger' : team.tier == 2 ? 'border-warning' : 'border-secondary']"
  >
    <h4
      v-once
      class="clickable text-white"
      data-toggle="collapse"
      :data-target="'#' + getName(team.name)"
    >
      <span
        class="badge"
        :class="[team.tier == 1 ? 'badge-danger' : team.tier == 2 ? 'badge-warning' : 'badge-secondary']"
      >{{ getTier(team.tier) }}</span> &nbsp;
      <div v-translate="team.name" class="d-inline">
        {{ team.name }}
      </div>
      <!-- {{ team.name }} -->
    </h4>
    <div
      id="teamList"
      class="clickable row"
      data-toggle="collapse"
      :data-target="'#' + getName(team.name)"
      @click="showComp = true"
    >
      <!-- <div v-for="c in team.characters" :key="uuid(c)">{{c}}</div> -->
      <div class="col-8 champ-list d-flex justify-content-start">
        <ChampIcon
          v-for="(c, index) in team.characters"
          v-once
          :id="'icon-' + getName(c.name)"
          :key="uuid(c)"
          class="mx-2 clickable"
          :champ="{name: c.name, set: [set], items: c.items}"
          :show-items="true"
          :index="index"
        />
      </div>

      <div class="ml-auto col-4">
        <div class="d-flex justify-content-center container-fluid">
          <Synergy v-for="s in team.synergy" v-once :key="uuid(s)" :sy="s" :team="team.characters" />
        </div>
        <!-- <h6 class="mt-4 pt-2 text-white text-center">TRAITS</h6> -->
        <h6 class="mt-4 pt-2 text-white text-center">
          ĐỘI HÌNH
        </h6>
      </div>
    </div>
    <div v-if="showComp" :id="getName(team.name)" class="collapse row">
      <div class="yard row mt-3 mb-3" style="height: 175px;">
        <ul id="hexGrid" style="width: 100%; margin: 0 auto;">
          <ChampHex
            v-for="p in 28"
            v-once
            :key="uuid(p)"
            :champs="team.characters"
            :index="p"
            :team-set="team.set[0]"
            :droppable="false"
          />
        </ul>
      </div>
      <div class="col-6">
        <div class="d-flex justify-content-center">
          <ChampIcon
            v-for="mid in team.mid"
            v-once
            :id="'icon-' + getName(mid)"
            :key="uuid(mid)"
            class="mx-1"
            :champ="{name: mid, set: [2]}"
            :width="'40px'"
          />
        </div>
        <!-- <h6 class="text-center text-white">CORE</h6> -->
        <h6 class="text-center text-white m-1">
          THIẾT YẾU
        </h6>

        <div class="replacable">
          <div
            v-for="rep in team.replacements"
            :key="uuid(rep)"
            class="d-flex justify-content-center mb-1"
          >
            <ChampIcon
              v-for="champ in rep.out"
              v-once
              :id="'icon-' + getName(champ)"
              :key="uuid(champ)"
              class="mx-1"
              :champ="{name: champ, set: [2]}"
              :width="'40px'"
            />
            <i class="fas fa-chevron-right btn m-1 text-white"></i>
            <ChampIcon
              v-for="champ in rep.in"
              v-once
              :id="'icon-' + getName(champ)"
              :key="uuid(champ)"
              class="mx-1"
              :champ="{name: champ, set: [2]}"
              :width="'40px'"
            />
          </div>
        </div>
        <!-- <h6 class="text-center text-white">REPLACEMENTS</h6> -->
        <h6 class="text-center text-white">
          THAY ĐỔI
        </h6>
      </div>
    </div>
  </div>
  <div v-else-if="set != 1" class="bg-dark">
    <div class="yard row mt-3 mb-3" style="height: 175px;">
      <ul id="hexGrid" style="width: 100%; margin-left: 15px;">
        <ChampHex v-for="p in 28" :key="uuid(p)" :droppable="true" :index="p" />
      </ul>
    </div>
  </div>
</template>

<script>
import ChampHex from "./ChampHex";
import ChampIcon from "./ChampIcon";
import Synergy from "./Synergy";

export default {
  name: "TeamComp",
  components: { ChampHex, ChampIcon, Synergy },
  props: {
    team: Object,
    set: Number
  },
  data() {
    return {
      isActive: false,
      showComp: false
    };
  },
  mounted() {
    // const observer = lozad(); // lazy loads elements with default selector as '.lozad'
    // observer.observe();
  },
  methods: {
    getTier: function(tier) {
      const t = {
        1: "S",
        2: "A",
        3: "B"
      };
      return t[tier];
    }
  }
};
</script>

<style>
.clickable {
  cursor: pointer;
}
.yard {
  width: 385px !important;
  margin: auto;
  padding: auto !important;
}

#hexGrid {
  max-width: 375px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin: 0 auto;
  overflow: hidden;
  font-family: Raleway, sans-serif;
  font-size: 15px;
  list-style-type: none;
  padding-bottom: 3%;
}

#hexGrid .hex:nth-child(14n + 1) {
  margin-left: 6%;
}
</style>