import Vue from "vue";
import { translated } from "../assets/translate";


const trans = window.translated || translated;
window.translate = translated || {};
Vue.directive("translate", {
  bind: function(el, binding, vnode) {
    window.translate[binding.value] = window.translate[binding.value] || '';
    if (binding.value) {
      el.innerText = trans[binding.value] || binding.value;
    }
  }
});
