<template>
  <div id="equip" class="container-fluid" style="overflow: auto; height: 100vh">
    <div class="row">
      <div
        v-for="item in items.filter(i => i.set.indexOf(2) > -1)"
        id="listItem"
        :key="uuid(item)"
        class="col-3 p-0"
      >
        <img
          v-once
          :id="'item-' + r"
          class="lozad m-1"
          :src="getItemURL(item.name)"
          width="45px"
          data-toggle="tooltip"
          data-html="true"
          data-placement="auto"
          :data-original-title="infoItem[item.name]"
        />
        <div class="d-none">
          <ItemInfo
            v-once
            :id="'itemInfo-' + r + getName(item.name)"
            :name="item.name"
            :type="isCombineOrInfo(item)"
            :data-item-name="item.name"
            :r="r"
          />
        </div>
      </div>
    </div>
    <!-- <ItemInfo
      v-once
      :id="'itemInfo-' + r + getName(item.name)"
      :name="item.name"
      :type="isCombineOrInfo(item)"
       v-for="item in items" :key="uuid(item.name)"
    />-->
  </div>
</template>

<script>
import ItemInfo from "./ItemInfo";

export default {
  name: "Items",
  components: {
    ItemInfo
  },
  data() {
    return {
      r: this.uuid(1),
      infoItem: {}
    };
  },
  mounted() {
    let that = this;
    if (document.querySelectorAll(`[id*=itemInfo-${this.r}]`).length) {
      for (let index = 0; index < document.querySelectorAll(`[id*=itemInfo-${this.r}]`).length; index++) {
        const element = document.querySelectorAll(`[id*=itemInfo-${this.r}]`)[index];
        that.infoItem[element.dataset.itemName] = element.innerHTML;
        // element.parentElement.remove();
        this.$forceUpdate();
      }
      // document.querySelectorAll(`[id*=itemInfo-${this.r}]`).forEach(e => {
      //   that.infoItem[e.dataset.itemName] = e.innerHTML;
      //   e.parentElement.remove();
      // });
    }
    this.$forceUpdate();
    let buttonsWithOpenTooltips = [],
      mousedOverTooltip,
      isTooltipClickablePreference = true;

    function hideOpenTooltips() {
      for (let button of buttonsWithOpenTooltips) {
        $(button).tooltip("hide");
      }
      buttonsWithOpenTooltips = [];
    }
    // Object.keys(this.itemsObject).forEach(e => {
    setTimeout(() => {
      console.log(111111111)
      $(`#listItem [id*=item-][data-toggle=tooltip][data-original-title]`)
        .tooltip({ trigger: "manual", boundary: "window" })
        .on("mouseenter touchend focus", function() {
          // Hide all other open tooltips
          buttonsWithOpenTooltips.push(this);

          $(this).tooltip("show");

          $(".tooltip")
            .on("mouseenter focus", function() {
              // Track last button moused over
              mousedOverTooltip = this;
            })
            .on("mouseleave", function() {
              // Hide this tooltip when user mouses out of it
              mousedOverTooltip = null;
              hideOpenTooltips();
            })
            .on("click", function() {
              // Hide this tooltip when user clicks it
              hideOpenTooltips();
            });
        })
        .on("mouseleave", function() {
          // .setTimeout needed because tooltip mouseenter fires after button mouseleave
          let _this = this;
          // isTooltipClickablePreference = uiUtils.getPreference('tooltip-clickable', false);
          if (isTooltipClickablePreference) {
            window.setTimeout(function() {
              if (
                $(".tooltip").length > 0 &&
                !$(".tooltip").is($(mousedOverTooltip))
              ) {
                $(_this).tooltip("hide");
              }
            }, 100);
          } else {
            $(_this).tooltip("hide");
          }
        });
      // $("[id*=item-" + this.r + "][data-toggle=tooltip]").tooltip();
      // })
    }, 1000);
  },
  methods: {
    isCombineOrInfo: function(item) {
      return item.into ? "into" : "combine";
    }
  }
};
</script>

<style>
#equip::-webkit-scrollbar {
  width: 5px;
}

#equip::-webkit-scrollbar-track {
  background: #ddd;
}

#equip::-webkit-scrollbar-thumb {
  background: #666;
}
</style>