<template>
  <li class="hex">
    <div class="hexIn">
      <div
        class="hexLink"
        style="background: rgb(27, 45, 51);background-size: cover"
        @drop="drop($event)"
        @dragover="dragover($event)"
        @click="removeChamp()"
      >
        <div class="rounded-circle" style="width: 130%;">
          <div class="character-wraper">
            <ChampIcon
              v-if="champ.name"
              v-once
              :id="'icon-' + getName(champ.name)"
              class="mx-2 hex-champ"
              :champ="champ"
            />
            <!-- <img
              :src="champURL"
              v-if="teamSet == 2"
              class="rounded-circle"
              style="width: 100%; border-style: none; border-color: rgb(204, 51, 255);"
            />-->
          </div>
        </div>
      </div>
    </div>
  </li>
</template>

<script>
import ChampIcon from "./ChampIcon";

export default {
  name: "ChampHex",
  components: { ChampIcon },
  props: {
    champs: Array,
    index: Number,
    teamSet: Number,
    droppable: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isPosition: false,
      champ: {},
      champURL: ""
    };
  },
  created() {
    // const observer = lozad(); // lazy loads elements with default selector as '.lozad'
    // observer.observe();
  },
  mounted() {
    if (this.champs) {
      this.champs.forEach(c => {
        let p = +c.position.replace("p", "");
        if (p == this.index + 1) {
          this.champ = c;
          this.champURL = this.getAvatarURL(c.name, this.teamSet);
          this.isPosition = true;
          return;
        }
      });
    }
  },
  methods: {
    checkPosition: function(pos) {
      pos = +pos.replace("p", "");
      if (this.index === pos) return true;
      return false;
    },
    drop: function(ev) {
      if (this.droppable) {
        ev.preventDefault();
        let name = ev.dataTransfer.getData("name"),
          set = ev.dataTransfer.getData("set");
        console.log(name, set);
        if (name) {
          this.champURL = this.getAvatarURL(name, set);
        }
      }
    },
    dragover: function(ev) {
      ev.preventDefault();
    },
    removeChamp: function() {
      this.champURL = "";
    }
  }
};
</script>

<style>
.hex-champ {
  transform: translateX(calc(-50% + 17px));
}
.rounded-circle {
  width: 40px;
}
.hex {
  position: relative;
  visibility: hidden;
  outline: 1px solid transparent;
}

.hexIn {
  -webkit-transform: rotate(-60deg) skewY(30deg);
  transform: rotate(-60deg) skewY(30deg);
}

.hexIn,
.xc {
  width: 90%;
  padding-bottom: 103.923%;
  margin: 0 2%;
  visibility: hidden;
}

.hexIn,
.hexLink,
.xc {
  overflow: hidden;
  outline: 1px solid transparent;
}

.hexLink {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  text-align: center;
  color: #fff;
  position: absolute;
  visibility: visible;
  -webkit-transform: skewY(-30deg) rotate(60deg);
  transform: skewY(-30deg) rotate(60deg);
}

#hexGrid .hex:nth-child(14n + 1) {
  margin-left: 6%;
}

.hex {
  position: relative;
  visibility: hidden;
  outline: 1px solid transparent;
  width: 12%;
}

.hexIn {
  width: 90%;
  padding-bottom: 103.923%;
  margin: 0 2%;
  visibility: hidden;
  -webkit-transform: rotate(-60deg) skewY(30deg);
  transform: rotate(-60deg) skewY(30deg);
}

.hexIn,
.hexLink {
  position: absolute;
  overflow: hidden;
  outline: 1px solid transparent;
}

.hexLink {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  text-align: center;
  color: #fff;
  visibility: visible;
  -webkit-transform: skewY(-30deg) rotate(60deg);
  transform: skewY(-30deg) rotate(60deg);
}
</style>