/* eslint-disable */
import Vue from "vue";

Vue.mixin({
  methods: {
    getAvatarURL(name, set) {
      // return name + set
      return set == 2
        ? `https://rerollcdn.com/characters/${set}/${name.replace(
            / /g,
            ""
          )}.png`
        : `https://rerollcdn.com/characters/${name.replace(/ /g, "")}.png`;
    },
    getItemURL(item) {
      // return 231232;
      return `https://rerollcdn.com/items/${item
        .replace(/ /g, "")
        .replace(/\./g, "")
        .replace(/'/g, "")}.png`;
    },
    getSkillURL(name, skillname) {
      return `https://rerollcdn.com/abilities/2/${name
        .toLowerCase()
        .replace(/ /g, "-")}-${skillname
        .toLowerCase()
        .replace(/ /g, "-")
        .replace(/['!]/g, "")}.png`;
    },
    getIconURL(icon) {
      return `https://rerollcdn.com/icons/${icon.toLowerCase()}.png`;
    },
    getItemInfo(item) {},
    randomKey: function() {
      return Math.random();
    },
    uuid: function(l) {
      if (typeof l !== "number") l = 5;
      l = (l < 0 ? 1 : l > 5 ? 5 : l) || 5;
      var x = {
        1: [1e7] + "",
        2: [1e7] + -1e3,
        3: [1e7] + -1e3 + -4e3,
        4: [1e7] + -1e3 + -4e3 + -8e3,
        5: [1e7] + -1e3 + -4e3 + -8e3 + -1e11
      };
      return x[l].replace(/[018]/g, function(c) {
        return (
          c ^
          (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
        ).toString(16);
      });
    },
    getChamp: function(listChamp, name) {
      return listChamp.filter(c => c.name == name && c.set[0] == 2)[0];
    },
    getTierColor: function(cost) {
      const t = {
        1: "#888 !important",
        2: "#156831 !important",
        3: "#12407c !important",
        4: "#893088 !important",
        5: "#b89d27 !important",
        7: "linear-gradient(to bottom right,#b827fc 0,#2c90fc 25%,#b8fd33 50%,#fec837 75%,#fd1892) !important"
      };
      return t[cost];
    },
    getName: function(name) {
      return name
        .replace(/ /g, "")
        .replace(/\./g, "")
        .replace(/'/g, "");
    },
    isOriginOrClass: function(name) {
      if (this.originObject.hasOwnProperty(name)) {
        return "origin";
      } else {
        return "class";
      }
    },
    iconCountF: function() {
      window.iconCount = window.iconCount + 1;
    }
  }
});
