<template>
  <div>
    <div class="hex synergy text-white">
      <div class="hexIn">
        <div class="hexLink" :class="sy.rank">
          <img
            :id="'trait-' + sy.name"
            class="trait lozad"
            :src="getIconURL(sy.name)"
            :alt="sy.name"
            data-toggle="tooltip"
            data-placement="auto"
            data-html="true"
            :data-original-title="info"
          />
          <!-- <span class="sy-count">{{ sy.count }}</span> -->
        </div>
      </div>
    </div>

    <div class="d-none">
      <TraitInfo v-once :id="'traitInfo-' + sy.name" :name="sy.name" :type="isOriginOrClass(sy.name)" :sy="sy" :team="team" />
    </div>
  </div>
</template>

<script>
import TraitInfo from "./TraitInfo";

export default {
  name: "Synergy",
  components: { TraitInfo },
  props: {
    sy: Object,
    team: Array
  },
  data() {
    return {
      info: ""
    };
  },
  created() {
    // const observer = lozad(); // lazy loads elements with default selector as '.lozad'
    // observer.observe();
  },
  mounted() {
    if (document.querySelector("#traitInfo-" + this.sy.name)) {
      this.info = document.querySelector(
        "#traitInfo-" + this.sy.name
      ).outerHTML;
      document.querySelector(
        "#traitInfo-" + this.sy.name
      ).parentElement.remove();
    }
    let buttonsWithOpenTooltips = [],
      mousedOverTooltip,
      isTooltipClickablePreference = true;

    function hideOpenTooltips() {
      for (let button of buttonsWithOpenTooltips) {
        $(button).tooltip("hide");
      }
      buttonsWithOpenTooltips = [];
    }
    $(`#trait-${this.sy.name}[data-toggle=tooltip]`)
      .tooltip({
        trigger: "manual",
        boundary: "window"
      })
      .on("mouseenter touchend focus", function() {
        // Hide all other open tooltips
        buttonsWithOpenTooltips.push(this);

        $(this).tooltip("show");

        $(".tooltip")
          .on("mouseenter focus", function() {
            // Track last button moused over
            mousedOverTooltip = this;
          })
          .on("mouseleave", function() {
            // Hide this tooltip when user mouses out of it
            mousedOverTooltip = null;
            hideOpenTooltips();
          })
          .on("click", function() {
            // Hide this tooltip when user clicks it
            hideOpenTooltips();
          });
      })
      .on("mouseleave", function() {
        // .setTimeout needed because tooltip mouseenter fires after button mouseleave
        let _this = this;
        // isTooltipClickablePreference = uiUtils.getPreference('tooltip-clickable', false);
        if (isTooltipClickablePreference) {
          window.setTimeout(function() {
            if (
              $(".tooltip").length > 0 &&
              !$(".tooltip").is($(mousedOverTooltip))
            ) {
              $(_this).tooltip("hide");
            }
          }, 100);
        } else {
          $(_this).tooltip("hide");
        }
      });
  }
};
</script>

<style scoped>
.synergy {
  width: 30px;
}
.sy-count {
  position: absolute;
}
img {
  width: 19px;
}
.bronze {
  background-color: #6f4939;
}

.silver {
  background-color: #6b6b6b;
}

.gold {
  background-color: #b89d27;
}
</style>